a {
    text-decoration: none;
    cursor: pointer;
    font-size: 30px;
}

.bottom a {
    color: var(--ac-white);
    margin: auto;
    font-size: 1em;
    vertical-align: middle;
    
}

#red, #green, #blue {
    margin: 0px 010px 5px 10px;

    background-color: var(--ac-primary);
    padding: 0.6em;
    border-radius: 100px;
    width: 80px;
    display: inline-block;
}

#click {
    color: var(--ac-text-gray);
}

#red:hover, #green:hover, #blue:hover {
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
}

#red{
    -webkit-transition: background-color 350ms,
                -webkit-transform 350ms;
    transition: background-color 350ms,
                -webkit-transform 350ms;
    transition: background-color 350ms,
                transform 350ms;
    transition: background-color 350ms,
                transform 350ms,
                -webkit-transform 350ms;
}

#red:hover {
    background-color: var(--ac-red);
}

#green{
    -webkit-transition: background-color 350ms,
                -webkit-transform 350ms;
    transition: background-color 350ms,
                -webkit-transform 350ms;
    transition: background-color 350ms,
                transform 350ms;
    transition: background-color 350ms,
                transform 350ms,
                -webkit-transform 350ms;
}

#green:hover {
    background-color: var(--ac-green);
}

#blue{
    -webkit-transition: background-color 350ms,
                -webkit-transform 350ms;
    transition: background-color 350ms,
                -webkit-transform 350ms;
    transition: background-color 350ms,
                transform 350ms;
    transition: background-color 350ms,
                transform 350ms,
                -webkit-transform 350ms;
}

#blue:hover {
    background-color: var(--ac-signature-blue);
    
    
}

.tc-link{
    vertical-align: middle;
    text-decoration: none;
    -webkit-transition: color .15s ease-in;
    transition: color .15s ease-in;
    color: white;
    font-size: 0.9rem;
    display: inline-block;
    padding-bottom: 0.2rem;
}

@media only screen and (max-width: 1250px) {
    #red:hover, #green:hover, #blue:hover {
        -webkit-transform: none;
                transform: none;
    }
    #red{
        background-color: var(--ac-red);
    }
    #red:hover {
        -webkit-transition: none;
        transition: none;
        
    }
    #green{
        background-color: var(--ac-green);
    }
    #green:hover {
        -webkit-transition: none;
        transition: none;
    }
    #blue{
        background-color: var(--ac-blue);
    }    
    #blue:hover {
        -webkit-transition: none;
        transition: none;
        
    }
    .bottom{
        margin-top: 20px;
    }
}

@-webkit-keyframes glow-bkg {
    0% {
        -webkit-filter: drop-shadow(0px 0px 9px var(--ac-primary));
                filter: drop-shadow(0px 0px 9px var(--ac-primary));
    }
    50% {
        -webkit-filter: drop-shadow(0px 0px 18px var(--ac-red));
                filter: drop-shadow(0px 0px 18px var(--ac-red));
        
    }
    100% {
        -webkit-filter: drop-shadow(0px 0px 9px var(--ac-primary));
                filter: drop-shadow(0px 0px 9px var(--ac-primary));
    }
}

@keyframes glow-bkg {
    0% {
        -webkit-filter: drop-shadow(0px 0px 9px var(--ac-primary));
                filter: drop-shadow(0px 0px 9px var(--ac-primary));
    }
    50% {
        -webkit-filter: drop-shadow(0px 0px 18px var(--ac-red));
                filter: drop-shadow(0px 0px 18px var(--ac-red));
        
    }
    100% {
        -webkit-filter: drop-shadow(0px 0px 9px var(--ac-primary));
                filter: drop-shadow(0px 0px 9px var(--ac-primary));
    }
}
@-webkit-keyframes glow-bkg-mob {
    0% {
        -webkit-filter: drop-shadow(0px 0px 3px var(--ac-primary));
                filter: drop-shadow(0px 0px 3px var(--ac-primary));
    }
    50% {
        -webkit-filter: drop-shadow(0px 0px 6px var(--ac-red));
                filter: drop-shadow(0px 0px 6px var(--ac-red));
        
    }
    100% {
        -webkit-filter: drop-shadow(0px 0px 3px var(--ac-primary));
                filter: drop-shadow(0px 0px 3px var(--ac-primary));
    }
}
@keyframes glow-bkg-mob {
    0% {
        -webkit-filter: drop-shadow(0px 0px 3px var(--ac-primary));
                filter: drop-shadow(0px 0px 3px var(--ac-primary));
    }
    50% {
        -webkit-filter: drop-shadow(0px 0px 6px var(--ac-red));
                filter: drop-shadow(0px 0px 6px var(--ac-red));
        
    }
    100% {
        -webkit-filter: drop-shadow(0px 0px 3px var(--ac-primary));
                filter: drop-shadow(0px 0px 3px var(--ac-primary));
    }
}

.BackgroundLogo {
    -webkit-animation: glow-bkg 6000ms ease-in-out infinite;
            animation: glow-bkg 6000ms ease-in-out infinite;
    width: 40%;
    margin: 6%;
    margin-bottom: 2%;
}

@media only screen  and (max-width: 1250px){
    .fixed-container{
        position: relative;
        top: 0;
        left: 0;
    }
    .BackgroundLogo {
        /* margin-left: -32px; */
        /* animation: glow-bkg-mob 6000ms ease-in-out infinite; */
        -webkit-animation: none;
                animation: none;
        -webkit-filter: drop-shadow(0px 0px 6px var(--ac-red));
                filter: drop-shadow(0px 0px 6px var(--ac-red));
        margin-bottom: 3%;
    
    }

    #welcome, #intro, #portfolio{
        font-size: 1rem;
    }
}
.home-container{
    display: grid;
    grid-template-columns: 16.665% 16.665% 16.665% 16.665% 16.665% 16.665%;
    grid-template-rows: 16.665% 16.665% 16.665% 16.665% 16.665% 16.665%; 
    
}

#intro-card{
    grid-column: 1 / 3;
    grid-row: 2 / 4;

}

#background-plate{
    grid-column: 2 / 6;
    grid-row: 1/6;
    align-self: center;
}

#about-card{
    grid-column: 5 / 6;
    grid-row: 1 / 5;
}

#theme-container{
    grid-column: 3 / 5;
    grid-row: 6;
    align-self: flex-end;
}

@media only screen  and (max-width: 1250px){
    .home-container{
        display: block;
    }

}
